body {
  @include mp-reset;
  min-width: 320px;
  max-width: 1920px;
  margin-left: auto;
  margin-right: auto;

  font-family: $font-family-Roboto-Condensed;
  font-size: 17px;
  line-height: 20px;
  color: white;
  font-style: normal;

  background-color: $bg;
}

a {
  text-decoration: none;
  cursor: pointer;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

button {
  margin: 0;
  padding: 0;
  display: block;
  text-decoration: none;

  cursor: pointer;
  outline: none;
}
