@mixin mp-reset {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@mixin list-reset {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin text {
  margin: 0;
  padding: 0;
  display: inline-block;
  vertical-align: middle;
}

@mixin a {
  margin: 0;
  padding: 0;
  display: inline-block;
  vertical-align: middle;
}

@mixin button {
  margin: 0;
  padding: 0;
  display: block;
  text-decoration: none;
  outline: none;
  cursor: pointer;
}
